
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
:root{
  --white :#fff;
  --black : #000;
  --main : #ff6e68;
  --lightblack: #2c2c2c;

}

body{
  font-family: 'Open Sans', sans-serif;
}
.container{
  width: 1300px;
  margin: 0 auto;
}
.nav{
  width: 100%;
  height: 70px;
  box-shadow: 0 5px 20px 2px rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  background: var(--white);
  z-index: 1;
}
.nav_container{
  width: 100%;
  display: flex;
  align-items: center;
  height: 70px;
  justify-content: space-between;
}

.nav_left img{
  width: 100px;
}

a{
  text-decoration: none;
}
.cart-icon{
  color: var(--main);
  font-size: 22px;
}
.basket{
  position: absolute;
}

.basket span{
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: -15px;
  top: -10px;
  height: 25px;
  width: 25px;
  background: var(--lightblack);
   border-radius: 50%;
   color: var(--white);
   font-size: 12px;
}

.row{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
}
.col-2{
  width: 16.33333333333333%;
}
.col-3{
  width: 25%;
}
.col-6{
  width: 50%;
}
.col-9{
  width: 75%;
}
.mt-100{
  margin-top: 100px;
}
.mt-10{
  margin-top: 10px;
}
.mb-40{
  margin-bottom: 40px;
}
/* products */
.product{
  padding: 25px;
  width: 100%;
  overflow: hidden;
}
.product__img{
  width: 100%;
  height: 400px;
  overflow: hidden;
}
.product__img img{
  width: 100%;
  height: 100%;
  transition: all .5s linear;
}
.product__img img:hover{
  transform: scale(1.2);
}

.product__name{
  margin: 10px 0;
  text-transform: capitalize;
  color: #707070;
}
.product_discount_price{
  text-align: right;
  font-size: 18px;
}
.product__price .discount{
  background-color: red;
  color: white;
  font-size: 0.9rem;
  font-weight: 400;
  margin-left: 4px;
  padding: 0.15rem;
}
.actualPrice{
  text-decoration: line-through;
  font-size: 18px;
}
/* details */
.details__image{
  width: 100%;
  height: auto;
  padding-right: 30px;
}
.details__image img{
  width: 100%;
  height: auto;
  margin-bottom: 25px;
}
.details__name{
  font-size: 28px;
  color: #2c2c2c;
  font-weight: 400;
  text-transform: capitalize;
}
.details__actual{
  text-decoration: line-through;
  font-size: 20PX;
}
.details__discount{
  margin-left: 20px;
  font-size: 20px;
}
.details__prices{
  margin: 10px 0;
}
.details__incDec{
  display: flex;
}
.dec,
.inc,
.quantity{
  width: 60px;
  height: 60px;
  border: 1px solid silver;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity{
  border-left: none;
  border-right: none;
}
.inc,.dec{
  cursor: pointer;
}
.btn-default{
  margin-left: 10px;
  width: 100%;
  border: none;
  background: var(--main);
  color:var(--white);
  outline: none;
  font-size: 18px;
  text-transform: uppercase;
  cursor: pointer;
}
.details__p{
  margin-top: 15px;
}
.details__p h4{
  font-weight: 600;
  margin-bottom: 10px;
  font-size: 18px;
}
/* cart */
.cart{
  margin-top: 100px;
}
.cart h3{
font-weight: 400;
}
.cart__heading{
  background: #f4f6f8;
}
.cart__image img{
  width: 100px;
  height: 100px;
  object-fit: contain;
}
.text-center{
  text-align: center;
}
.verticalAlign{
  align-items: center;
  margin-top: 10px;
}
.cart__remove{
  cursor: pointer;
}
.summary{
  padding-left: 10px;
}
.summary__heading{
  background: #f4f6f8;
  padding: 10px;
}
.summary__details{
  background: var(--main);
  color: var(--white);
  padding: 15px;

}
.mb-10{
  margin-bottom: 10px;
}
.checkout{
  border: none;
  cursor: pointer;
  outline: none;
  background: var(--white);
  color: var(--main);
  width: 100%;
  padding: 12px 5px;
  font-size: 17px;
  text-transform: uppercase;
}
/*********** Footer ***************/
.main-footer {
  /* background-color: var(--main); */
  justify-content: center;
  text-align: center;
  /* position: fixed; */
  height: 150px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  margin-bottom: 0px;
}
.main-footer  li,h4,p,h6{
  color: var(--primary-white);
}
.list-unstyled{
  list-style-type: none;
}
.logo-footer{
  width: 150px;
}
@media(max-width:765px){
  .container{
    width: 100%;
    padding: 0 30px;
  }
  .col-2{
    width: 100%;
    text-align: center ;
    padding: 10px;
  }
  .col-3{
    width: 50%;
  }
  .col-6{
    width: 100%;
  }
  .col-9{
    width: 100%;
  }
  
  .details__image{
    padding-right: 0;
  }
  .cart__heading{
    display: none;
  }
  .summary-col{
    width: 100% !important;
  }
  .cart__incDec{
    display: flex;
    justify-content: center;
  }
  .cart h3{
    text-align: center;
  }
  .header{
    padding-top: 60px;
  }
  
}

@media(max-width: 400px){
  .col-2{
    width: 100%;
  }
  .col-3{
    width: 100%;
  }
  .col-6{
    width: 100%;
  }
  .details__image{
    padding-right: 0;
  }
  
}